import { GenerationFailure } from "./TimeTablesGenerator";
import * as React from "react";

type InvalidTipeTablesProps = { failure: GenerationFailure, dismissError: () => void };
export const InvalidTimeTablesSettings = ({ failure, dismissError } : InvalidTipeTablesProps) => {
  return <>
    <h1>Invalid Settings: {failure.message}</h1>
    <button id="dismissError" onClick={dismissError}>Dismiss Error</button>
  </>
};

