import { Either, Left, Right } from "../../../functional-helpers/Either";
import { Seconds, TimeTablesSettings } from "../TimeTablesSettings";


export const BreakSessionType = "Break";
export const TrainingSessionTime = "Workout";
export const MinimumSessionTime = 10;

export type TimeTableSessionType = "Break" | "Workout";
export interface TimeInterval {
  sessionType: TimeTableSessionType
  totalTime: number
  notifications: number[]
  sessionIndex: number
}

export interface GenerationFailure {
  message: string
}
export const InvalidTrainingStartTime: GenerationFailure = { message: `Can't have less than ${MinimumSessionTime} seconds workout start time.` };
export const InvalidBreakStartTime: GenerationFailure = { message: `Can't have less than ${MinimumSessionTime} seconds break start time.` };

function isInvalidSessionStartTime(sessionStartTime: Seconds, minimumTime: Seconds = MinimumSessionTime): boolean {
  return sessionStartTime < minimumTime || isNaN(sessionStartTime);
}

function generateTimeTables(settings: TimeTablesSettings): Either<GenerationFailure, TimeInterval[]> {
  if (isInvalidSessionStartTime(settings.trainingStartTime)) {
    return new Left(InvalidTrainingStartTime);
  } else  if (isInvalidSessionStartTime(settings.breakStartTime)) {
    return new Left(InvalidBreakStartTime);
  } else {
    return new Right(calculateTimeTables(settings));
  }
}

function valueOrMinimum(v: number) {
  return Math.max(v, MinimumSessionTime)
}

function calculateTimeTables(settings: TimeTablesSettings, currentInterval: TimeInterval[] = []): TimeInterval[] {
  if(settings.numberOfReps <= 0) {
    return currentInterval;
  } else {
    const newIntervals: TimeInterval[] = [
      {
        sessionType: BreakSessionType,
        totalTime: settings.breakStartTime,
        notifications: settings.breakTimeNotifications,
        sessionIndex: currentInterval.length
      },
      {
        sessionType: TrainingSessionTime,
        totalTime: settings.trainingStartTime,
        notifications: settings.trainingTimeNotifications,
        sessionIndex: currentInterval.length + 1
      }
    ];
    let breakStartTime = valueOrMinimum(settings.breakStartTime + settings.breakTimeIncrement);
    let trainingStartTime = valueOrMinimum(settings.trainingStartTime + settings.trainingTimeIncrement);
    const nextSession = {
      breakStartTime: breakStartTime,
      trainingStartTime: trainingStartTime,
      numberOfReps: settings.numberOfReps - 1
    };

    return calculateTimeTables({...settings, ...nextSession}, [...currentInterval, ...newIntervals])
  }
}
export default generateTimeTables;
