import * as React from 'react';
import CircularProgress, {
  CircularProgressProps,
} from '@mui/material/CircularProgress';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';

export interface CircularProgressWithLabelProps extends CircularProgressProps {
  value: number
  totalValue: number
  label?: string
}

export const CircularProgressWithLabel: React.FunctionComponent<CircularProgressWithLabelProps> = (
  props: CircularProgressWithLabelProps
) => {
  const { value, totalValue, label } = props;
  const percentage = Math.floor((value / totalValue) * 100);
  const labelValue = label || `${Math.round(percentage)}%`
  const circularProgressProps = { ...props, value: percentage }

  return (
    <Box
      sx={{
        position: 'relative',
        display: 'inline-flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: "100%",
      }}
    >
      <CircularProgress variant="determinate" {...circularProgressProps} />
      <Box
        sx={{
          top: 0,
          left: 0,
          bottom: 0,
          right: 0,
          width: '100%',
          position: 'absolute',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
        }}
      >
        <Typography
          variant="caption"
          component="div"
          color="text.secondary"
          fontSize="2vw"
          fontFamily="monospace"
        >{labelValue}</Typography>
      </Box>
    </Box>
  );
}
