const getAvailableVoice = () => {
  let voices = window.speechSynthesis.getVoices();
  return findVoice(voices, "Alex") ||
    findVoice(voices, "Samantha") ||
    findVoice(voices, "Nicky") ||
    findVoice(voices, "Google UK English Male") ||
    voices.filter(voice => voice.lang === "en-US")[0];
};

const findVoice = (voices: SpeechSynthesisVoice[], voiceName: string) =>
  voices.find(v => v.name === voiceName);

const getUtter = (message: string, voice: SpeechSynthesisVoice) => {
  const utter = new SpeechSynthesisUtterance(message);
  utter.rate = 1;
  utter.pitch = 0.5;
  utter.voice = voice;
  return utter;
};

export function notifyUser(message: string) {
  const voice = getAvailableVoice();
  console.log('voice', voice);
  console.log('allVoices', window.speechSynthesis.getVoices());
  window.speechSynthesis.speak(getUtter(message, voice));
}
