import * as React from 'react';
import { TimeTablesSettings } from "../TimeTablesSettings";
import generateTimeTables, { TimeInterval } from "./TimeTablesGenerator";
import { PracticeSummary } from "../../PracticeSummary";
import { InvalidTimeTablesSettings } from "./InvalidTimeTablesSettings";
import { StartPractice } from "../../StartPractice";
import { useState } from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import { ButtonGroup } from '@mui/material';

interface TimeTablesPracticeProps {
  settings: TimeTablesSettings,
  onCancelPractice: () => void
}

const TimeTablesPractice: React.FunctionComponent<TimeTablesPracticeProps> = ({ settings, onCancelPractice }) => {
  const timeIntervals = generateTimeTables(settings)
  const [startPractice, setStartPractice] = useState(false)

  const onStartPractice = () => {
    setStartPractice(true)
  }
  const renderPractice = (timeIntervals: TimeInterval[]) => {
    if (startPractice) {
      return <StartPractice timeIntervals={timeIntervals}/>
    } else {
      return <PracticeSummary timeIntervals={timeIntervals}/>
    }
  }

  return timeIntervals.fold(
    failure => <InvalidTimeTablesSettings failure={failure} dismissError={onCancelPractice}  />,
    timeIntervals => {
      return (
        <Box>
          {renderPractice(timeIntervals)}
          <ButtonGroup variant="contained" aria-label="outlined primary button group">
            { !startPractice &&
             <Button color="success" onClick={onStartPractice}>Start Practice</Button>
             }
            <Button variant="contained" onClick={onCancelPractice}>Cancel</Button>
          </ButtonGroup>
        </Box>
      )
    }
  )

}

export default TimeTablesPractice;
