import * as React from 'react';
import { SyntheticEvent } from "react";
import { NotificationSettings, TimeTablesSettings, setTrainingTimeIncrement, setTrainingTimeNotifications, setTrainingStartTime, setBreakTimeIncrement, setBreakTimeNotifications, setBreakStartTime, setNumberOfReps } from '../TimeTablesSettings';
import { useSelector, useDispatch } from 'react-redux'
import { RootState } from '../../../store';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Divider from '@mui/material/Divider';
import { AddNotificationForm } from './AddNotificationForm';

interface TimeTablesFormProps {
  onSubmit: (state: TimeTablesSettings) => void;
}

const defaultNotificationTime = 30

const TimeTablesForm: React.FunctionComponent<TimeTablesFormProps> = ({ onSubmit }) =>  {
  const reducerState: TimeTablesSettings = useSelector(
    (state: RootState) => state.timeTablesSettings
  );

  const dispatch = useDispatch();

  const updateTrainingTimeNotification = (notifications: NotificationSettings[]) => {
    dispatch(setTrainingTimeNotifications(notifications));
  }

  const updateBreakTimeNotification = (notifications: NotificationSettings[]) => {
    dispatch(setBreakTimeNotifications(notifications));
  }

  const onFormSubmit = (event: SyntheticEvent) => {
    event.preventDefault();
    onSubmit(reducerState);
  };

  const changeTrainingStartTime = (event: any) => {
    dispatch(setTrainingStartTime(parseInt(event.target.value)))
  };

  const changeTrainingTimeIncrement = (event: any) => {
    dispatch(setTrainingTimeIncrement(parseInt(event.target.value)))
  };

  const changeBreakStartTime = (event: any) => {
    dispatch(setBreakStartTime(parseInt(event.target.value)))
  };

  const changeBreakTimeIncrement = (event: any) => {
    dispatch(setBreakTimeIncrement(parseInt(event.target.value)))
  };

  const changeNumberOfReps = (event: any) => {
    dispatch(setNumberOfReps(parseInt(event.target.value)))
  }

  return (
    <Box>
      <Box>
        <Typography variant="h3">Sessions</Typography>
        <Divider/>
        <Box>
          <Typography variant="h4">Training Time</Typography>
          <Box>
            <TextField
              type="number"
              variant="standard"
              label="Start Time"
              name="trainingStartTime"
              value={reducerState.trainingStartTime}
              onChange={changeTrainingStartTime}
            />
          </Box>
          <Box>
            <TextField
              label="Time Increment"
              variant="standard"
              type="number"
              name="trainingTimeIncrement"
              value={reducerState.trainingTimeIncrement}
              onChange={changeTrainingTimeIncrement}
            />
          </Box>
        </Box>
        <Box>
          <AddNotificationForm
            updateNotifications={updateTrainingTimeNotification}
            currentNotifications={reducerState.trainingTimeNotifications}
          />
        </Box>
        <br />

        <Divider/>
        <Box>
          <Typography variant="h4">Break Time</Typography>
          <Box>
            <TextField
              label="Break Start Time"
              variant="standard"
              type="number"
              name="breakStartTime"
              value={reducerState.breakStartTime}
              onChange={changeBreakStartTime}
            />
          </Box>
          <Box>
            <TextField
              label="Break Time Increment"
              variant="standard"
              type="number"
              name="breakTimeIncrement"
              value={reducerState.breakTimeIncrement}
              onChange={changeBreakTimeIncrement}
            />
          </Box>
        </Box>
        <Box>
          <AddNotificationForm
            updateNotifications={updateBreakTimeNotification}
            currentNotifications={reducerState.breakTimeNotifications}
          />
        </Box>
        <br />
        <Divider/>
        <br />
        <Box>
          <TextField
            label="Number of Reps"
            variant="standard"
            type="number"
            name="numberOfReps"
            value={reducerState.numberOfReps}
            onChange={changeNumberOfReps}
          />
          <Button data-testid="submit-training-settings" color="success" variant="contained" onClick={onFormSubmit}>Submit</Button>
        </Box>
      </Box>
    </Box>
  )
}

export default TimeTablesForm;
