export const presentTime = (totalSeconds: number): string => {
  const hours = Math.floor(totalSeconds / 3600)
  const remainingSeconds = totalSeconds % 3600
  const minutes = Math.floor(remainingSeconds / 60)
  const secondsLeft = remainingSeconds % 60
  const hoursString = hours > 0 ? `${hours}:` : ''
  const minutesString = minutes < 10  && hours > 0 ? `0${minutes}` : `${minutes}`
  const secondsString = secondsLeft < 10 ? `0${secondsLeft}` : `${secondsLeft}`
  console.log('totalSeconds, hours', totalSeconds, hours)
  return `${hoursString}${minutesString}:${secondsString}`
}