import * as React from "react";
import { BreakSessionType, TimeInterval } from "../setup/TimeTablesGenerator";
import { notifyUser } from "../../notifications/NotifyUser";
import { useEffect, useState } from "react";
import { presentTime } from "../../timePresenter";
import { CircularProgressWithLabel } from "../../../components/CircularProgressWithLabel";
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';

type TimedSessionPresenterProps = { timeInterval: TimeInterval, onComplete: () => void };

export const TimedSessionPresenter: React.FunctionComponent<TimedSessionPresenterProps>  = ({ timeInterval, onComplete }) => {
  const { totalTime, notifications, sessionType } = timeInterval
  const [timeLeft, setTimeLeft] = useState(totalTime)

  useEffect(() => {
    counter(timeLeft)
  }, [timeLeft])

  useEffect(() => {
    counter(timeInterval.totalTime)
    onChange(timeInterval.totalTime)
  }, [timeInterval])

  const onChange = (remainingTime: number) => {
    const notification = notifications.find(n => n === remainingTime);
    if (notification) {
      notifyUser(`${notification} seconds remaining`);
    }
    setTimeLeft(remainingTime)
  };

  const counter = (timeLeft: number) => {
    if (timeLeft > 0) {
      setTimeout(() => {
        onChange(timeLeft - 1);
      }, 1000);
    } else {
      onComplete();
    }
  }

  const message = sessionType === BreakSessionType ? "Break time!" : "Workout Time!";
  return (
    <Box sx={{ width: '100%' }}>
      <Typography variant="h3" gutterBottom>{message}</Typography>
      <CircularProgressWithLabel
        className="circular-training-progress-bar"
        value={timeLeft}
        totalValue={totalTime}
        label={presentTime(timeLeft)}
        size="30%"
      />
    </Box>
  )
}
