import * as React from "react";
import { TimedSessionPresenter } from "./time-tables/sessions/TimedSessionPresenter";
import { BreakSessionType, TimeInterval } from "./time-tables/setup/TimeTablesGenerator";
import { notifyUser } from "./notifications/NotifyUser";
import { useState } from "react";
import Box from "@mui/material/Box";
import LinearProgress from "@mui/material/LinearProgress";
import { LinearProgressWithLabel } from "../components/LinearProgressWithLabel";

type StartPracticeProps = { timeIntervals: TimeInterval[] }

export const StartPractice: React.FunctionComponent<StartPracticeProps> = ({ timeIntervals }: StartPracticeProps) => {
  const [timeIntervalsLeft, setTimeIntervalsLeft] = useState<TimeInterval[]>(timeIntervals)
  const originalIntervalsCount = timeIntervals.length;


  const nextInterval = () => {
    const updated = timeIntervalsLeft.slice(1);
    setTimeIntervalsLeft(updated);
  }

  if (timeIntervalsLeft.length === 0) {
    notifyUser("Practice has ended");
    return <h1>Practice has ended</h1>;
  } else {
    const interval = timeIntervalsLeft[0];
    if (interval.sessionType === BreakSessionType) {
      notifyUser('Break time!');
    } else {
      notifyUser('Start training!')
    }
    const progress = Math.floor(((originalIntervalsCount - timeIntervalsLeft.length) / originalIntervalsCount) * 100);
    return (
      <Box>
        <LinearProgressWithLabel value={progress} />
        <TimedSessionPresenter timeInterval={interval} onComplete={nextInterval} />
      </Box>
    );
  }
}
