import * as React from "react";

import { TimeInterval } from "./time-tables/setup/TimeTablesGenerator";
import { presentTime } from "./timePresenter";

type PracticeSummarySettings = { timeIntervals: TimeInterval[] };

export const PracticeSummary = ({ timeIntervals }: PracticeSummarySettings) => {
  const totalTimeSeconds = timeIntervals.reduce((acc, value) => acc + value.totalTime, 0);
  return (
    <>
      <h2>Great Success</h2>
      <p>Practice Total Time: { presentTime(totalTimeSeconds) } </p>
      {timeIntervals.map(({sessionType, totalTime}, index) =>
        (
          <div key={index}>
            <p>Session: {sessionType}, totalTime: {totalTime}</p>
          </div>
        )
      )}
    </>
  )
};
