import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import TextField from '@mui/material/TextField';
import * as React from 'react';
import { SyntheticEvent, useState } from "react";
import { NotificationSettings } from '../TimeTablesSettings';
import AddCircle from '@mui/icons-material/AddCircle';
import DeleteForeverRounded from '@mui/icons-material/DeleteForeverRounded';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';

interface NotificationFormProps {
  updateNotifications: (notifications: NotificationSettings[]) => void
  currentNotifications: NotificationSettings[]
}

const baseNotifications = [
  30, 10, 5
];

export const AddNotificationForm: React.FunctionComponent<NotificationFormProps> = ({
  updateNotifications,
  currentNotifications
}) => {
  const [notificationTime, setNotificationTime] = useState<number | undefined>(undefined)

  const submitTrainingTimeNotification = (event: SyntheticEvent) => {
    if (notificationTime) {
      updateNotifications(addToNotificationsList(currentNotifications, notificationTime))
    }
  };

  const addToNotificationsList = (existing: NotificationSettings[], notification: NotificationSettings) => {
    if (existing.find(n => n === notification)) {
      return existing
    } else {
      return [...existing, notification]
    }
  }

  const addBaseNotifications = (event: SyntheticEvent) => {
    const updated = baseNotifications.reduce(addToNotificationsList, currentNotifications)
    updateNotifications(updated)
  }

  const changeNotificationTime = (event: any) => {
    setNotificationTime(parseInt(event.target.value));
  }

  const clearNotifications = () => updateNotifications([])

  return (
    <Box>
      <br />
      <Typography variant="h5">Voice Notifications</Typography>
      <TextField
        label="Voice Notification"
        type="number"
        name="notificationTime"
        variant="standard"
        value={notificationTime}
        onChange={changeNotificationTime}
        InputProps={{
          endAdornment: <>
            <Button onClick={submitTrainingTimeNotification}><AddCircle/></Button>
            <Divider sx={{ height: 28, m: 0.5 }} orientation="vertical" />
            <Button onClick={addBaseNotifications}>30,10,5</Button>
          </>
        }}
      />
      <Box>
        <Typography variant="body1">
          {currentNotifications.join(', ')}
          {currentNotifications.length > 0 &&
            <DeleteForeverRounded
              sx={{ color: 'red' }}
              onClick={clearNotifications}

            />
          }
        </Typography>
      </Box>
    </Box>
  );
}
