import { createSlice } from '@reduxjs/toolkit'
import type { PayloadAction } from '@reduxjs/toolkit'

export type Seconds = number
export type NotificationSettings = number

export interface TimeTablesSettings {
  trainingStartTime: Seconds;
  breakStartTime: Seconds;
  trainingTimeIncrement: Seconds;
  breakTimeIncrement: Seconds;
  numberOfReps: number;
  trainingTimeNotifications: NotificationSettings[];
  breakTimeNotifications: NotificationSettings[];
}

export const initialState: TimeTablesSettings = {
  trainingStartTime: 10,
  trainingTimeIncrement: 0,
  breakStartTime: 10,
  breakTimeIncrement: 0,
  numberOfReps: 2,
  trainingTimeNotifications: [],
  breakTimeNotifications: []
}

export const timeTablesSettingsSlice = createSlice({
  name: 'timeTablesSettings',
  initialState,
  reducers: {
    setTrainingStartTime: (state, action: PayloadAction<Seconds>) => {
      state.trainingStartTime = action.payload
    },
    setBreakStartTime: (state, action: PayloadAction<Seconds>) => {
      state.breakStartTime = action.payload
    },
    setTrainingTimeIncrement: (state, action: PayloadAction<Seconds>) => {
      state.trainingTimeIncrement = action.payload
    },
    setBreakTimeIncrement: (state, action: PayloadAction<Seconds>) => {
      state.breakTimeIncrement = action.payload
    },
    setNumberOfReps: (state, action: PayloadAction<number>) => {
      state.numberOfReps = action.payload
    },
    setTrainingTimeNotifications: (state, action: PayloadAction<NotificationSettings[]>) => {
      state.trainingTimeNotifications = action.payload
    },
    setBreakTimeNotifications: (state, action: PayloadAction<NotificationSettings[]>) => {
      state.breakTimeNotifications = action.payload
    }
  }
})

export const { setTrainingStartTime, setBreakStartTime, setTrainingTimeIncrement, setBreakTimeIncrement, setNumberOfReps, setTrainingTimeNotifications, setBreakTimeNotifications } =
  timeTablesSettingsSlice.actions

export default timeTablesSettingsSlice.reducer