import * as React from 'react'
import TimeTablesForm from "./time-tables/setup/TimeTablesForm"
import TimeTablesPractice from "./time-tables/setup/TimeTablesPractice"
import { useState } from 'react'
import { TimeTablesSettings, initialState } from './time-tables/TimeTablesSettings'

export const PracticeLandingPage: React.FunctionComponent = () => {
  const [displayTraining, setDisplayTraining] = useState(false);
  const [timeTablesSettings, setTimeTablesSettings] = useState<TimeTablesSettings>(initialState);

  const setSettings = (settings: TimeTablesSettings) => {
    setTimeTablesSettings(settings)
    setDisplayTraining(true)
  }

  const cancelPractice = () => setDisplayTraining(false)

  if (displayTraining) {
    return <TimeTablesPractice settings={timeTablesSettings} onCancelPractice={cancelPractice} />
  } else {
    return (
      <div>
        <TimeTablesForm onSubmit={setSettings} />
      </div>
    )
  }
}

export default PracticeLandingPage;
